/*
      escape   ,   encodeURI  ,   encodeURIComponent
unescape  ,   decodeURI  ,   decodeURIComponent

urlencodeURI
window.locaiton.href=encodeURI("http://cang.baidu.com/do/s?word=&ct=21");
encodeURI("http://cang.baidu.com/do/s?word=&ct=21") -> "http://cang.baidu.com/do/s?word=%E7%99%BE%E5%BA%A6&ct=21"
decodeURI("http://cang.baidu.com/do/s?word=%E7%99%BE%E5%BA%A6&ct=21") -> "http://cang.baidu.com/do/s?word=&ct=21"

escape69*+-./@_0-9a-zA-Z
encodeURI82!#$&'()*+,-./:;=?@_~0-9a-zA-Z
encodeURIComponent71! '()*-._~0-9a-zA-Z
*/
var tools = {};
//window.location.hrefwindow.location.search
tools.querySearch = search => {
  if (!!search && typeof search == "string") {
    let rstObj = {};
    /* search */
    let searchArr =
      search[0] !== "?"
        ? search.substr(search.indexOf("?") + 1).split("&")
        : search.substr(1).split("&");
    searchArr.forEach((each, index) => {
      let item = each.split("=");
      if (each.indexOf("=") != -1) {
        rstObj[item[0].trim()] = item[1].trim();
      } else {
        console.error(`${index + 1}=,`);
      }
    });
    return rstObj;
  } else {
    console.error("");
    return undefined;
  }
};
//
tools.deepCopy = source => {
  var result;
  source instanceof Array ? (result = []) : (result = {});

  for (var key in source) {
    result[key] =
      typeof source[key] === "object"
        ? this.deepCopy(source[key])
        : source[key];
  }
  return result;
};
//PCtrue
tools.isPC = () => {
  var userAgentInfo = window.navigator.userAgent;
  var Agents = [
    "Android",
    "iPhone",
    "SymbianOS",
    "Windows Phone",
    "iPad",
    "iPod"
  ];
  var flag = true;
  for (var v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false;
      break;
    }
  }
  return flag;
};
//
tools.isWX = () => {
  var ua = window.navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) == "micromessenger") {
    return true;
  } else {
    return false;
  }
};
//cookie
tools.setCookie = (name, value) => {
  var Days = 30; /* cookie */
  var exp = new Date();
  exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
  document.cookie =
    name + "=" + escape(value) + ";expires=" + exp.toGMTString();
};
//cookie
tools.getCookie = name => {
  var arr,
    reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if ((arr = document.cookie.match(reg))) {
    return unescape(arr[2]);
  } else {
    /* cookie */
    return document.cookie;
  }
};
/*
   cookie
   
   "BAIDUID=84010C5D99B330DFF6E5DE339C5B46A5:FG=1; PSTM=1506002955; BIDUPSID=50EB351368264907701A48B60DE2D43F"
  */
tools.queryCookie = str => {
  if (typeof str == "string") {
    if (str.indexOf(";") > -1) {
      const obj = {};
      str.split(";").forEach((item, index) => {
        if (item.indexOf("=") > -1) {
          obj[item.split("=")[0].trim()] = item.substr(item.indexOf("=") + 1);
        } else {
          console.error(`${index + 1}=`);
        }
      });
      return obj;
    } else {
      console.error('cookie";"');
    }
  } else {
    console.error("");
  }
};
//
tools.getStorage = key => {
  const re = /^\[|\{|\}|\]$/g; //[]{}
  let getIt = localStorage.getItem(key);
  if (re.test(getIt)) {
    return JSON.parse(getIt);
  } else {
    return getIt;
  }
};
//
tools.setStorage = (key, val) => {
  if (typeof val == "string") {
    //id
    localStorage.setItem(key, val);
  } else {
    //
    localStorage.setItem(key, JSON.stringify(val));
  }
};
