tools.verifyPhone = val => {
  const re = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
  return re.test(val) ? val : false;
};
//
//"2017.09.01 15:04:01"
tools.redate = (date, pattern = "yyyy-MM-dd") => {
  let fmt = pattern;
  var o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "H+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    S: date.getMilliseconds() //
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};
//
tools.add0 = num => {
  return num < 10 ? "0" + num : "" + num;
};
//(M,)
//lat  		lng 
tools.getDistance = (lat1, lng1, lat2, lng2) => {
  const EARTH_RADIUS = 6378137.0; //M
  const PI = Math.PI;
  let getRad = d => d * PI / 180;
  let f = getRad((lat1 + lat2) / 2);
  let g = getRad((lat1 - lat2) / 2);
  let l = getRad((lng1 - lng2) / 2);
  let sg = Math.sin(g);
  let sl = Math.sin(l);
  let sf = Math.sin(f);
  let s, c, w, r, d, h1, h2;
  let a = EARTH_RADIUS;
  let fl = 1 / 298.257;
  sg = sg * sg;
  sl = sl * sl;
  sf = sf * sf;
  s = sg * (1 - sl) + (1 - sf) * sl;
  c = (1 - sg) * (1 - sl) + sf * sl;
  w = Math.atan(Math.sqrt(s / c));
  r = Math.sqrt(s * c) / w;
  d = 2 * w * a;
  h1 = (3 * r - 1) / 2 / c;
  h2 = (3 * r + 1) / 2 / s;
  return d * (1 + fl * (h1 * sf * (1 - sg) - h2 * (1 - sf) * sg));
};

tools.getStyle = (element, attr, NumberMode = "int") => {
  let target;
  // scrollTop styledocument.body
  if (attr === "scrollTop") {
    target = element.scrollTop;
  } else if (element.currentStyle) {
    target = element.currentStyle[attr];
  } else {
    target = document.defaultView.getComputedStyle(element, null)[attr];
  }
  // opactiy  parseFloat
  return NumberMode == "float" ? parseFloat(target) : parseInt(target);
};
tools.debounce = function(fn, interval) {
    var timer, isFirst = true;
    return function() {
        var args = arguments,
            _me = this;
        if(isFirst) {
            fn.apply(_me, args);
            return isFirst = false;
        }
        console.log(timer)
        if(timer) {
            return false
        }
        timer = setTimeout(function() {
            clearTimeout(timer);
            timer = null;
            fn.apply(_me, args)
        }, interval || 500)
    }
}
export default tools;



